import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useSelector } from 'react-redux';

import PartyListItem from './PartyListItem';
import PartyDetail from './PartyDetail';

function PartyList ( ) {


    // Retrieve selected pokemon from global state
    const selectedDetail = useSelector( state => state.partyDetail );

    const currentUser = useSelector( state => state.currentUser );

    const [ party, setParty ] = useState( [] );

    useEffect( () => {

        axios({
            method: 'get',
            url: `https://us-central1-pokemon-412.cloudfunctions.net/retrieve-party?user_id=${ currentUser.uid }`,
            headers: { 'crossDomain': 'true' }
        }).then( results => {
            console.log('BUG TEST');
            setParty( results.data )
        });
    }, []);

    const partyListItems = party
        .map( ( pokemon, index ) =>
            <PartyListItem
                key={ index }
                pokemon={ pokemon }/>
        );

    const containerStyles = {

        // positioning
        display: 'flex',
        flexDirection: 'column',

        width: '60vw',
        height: '90vh'
    };


    const listContainerStyles = {

        display: 'grid',
        gridTemplateColumns: 'repeat(3, 1fr)',
        justifyItems: 'center',
    };

    const scaleView ={
        transform: 'scale(0.9)',

    };

    return(
        <div>
            
            <div style={ scaleView }>
                
                <div style={ containerStyles }>

                
                    <div style={ listContainerStyles }>
                        { partyListItems }
                    </div>
                    { /* Render conditionally based on whether a pokemon has been selected yet (selectedDetail != null) */ }
                    { selectedDetail && <PartyDetail pokemon={ selectedDetail }/> }
                </div>

            </div>
        </div>

        

    );
}

export default PartyList;
